/* Global */
input {
    line-height: 0;
}
div[class*='css-'] {
    .css-1hwfws3 {
        padding: 0;
    }
    div[class$='-control'] {
        box-shadow: none;
        border: none;
        min-height: 0;
    }
    div[class$='-singleValue'] {
        margin: 0;
    }
    div[class$='-Input'] {
        margin: 0;
        padding: 0;
    }
    div[class$='-Svg'] {
        position: absolute;
        right: 0;
        top: -3;
    }
    div[class$='-IndicatorsContainer'] {
        position: absolute;
        top: -12px;
        right: -6px;
        span[class$='-indicatorSeparator'] {
            display: none;
        }
    }
    
}











.detailed-dropdown {
    padding: 5px 0 5px 0;
    width: 100%;
    .text {
        color: #272E4C;
        opacity: 0.7;
        font-size: 11px;
    }
    .autocomplete {
        display: flex;
        .detail {
            background: #FFFFFF;
            border: 1px solid #272e4c4d;
            border-top-left-radius: 4px;
            border-bottom-left-radius: 4px;
            font-size: 11px;
            padding: 8px;
            width: 75px;
        }
        .text-field {
            border: 1px solid #272e4c4d;
            border-top-right-radius: 4px;
            border-bottom-right-radius: 4px;
            padding: 8px 8px;
            font-size: 11px;
            background: #FFFFFF;
            margin-left: -1px;
            width: 100%;
        }
    }
}





